<template>
  <div class="exist_address">
    <div class="edit_btn">
      <div class="edit_row">
        <input
          class="info_input input_name"
          type="text"
          placeholder="请输入姓名"
          v-model="recieverName"
          maxlength="24"
        />
        <input
          class="info_input input_phone"
          type="number"
          maxlength="11"
          placeholder="请输入电话号码"
          v-model="recieverMobile"
          oninput="if(value.length>11)value=value.slice(0,11)"
        />
      </div>
      <div class="edit_row">
        <div class="select1">
          <el-select
            v-model="prov"
            placeholder="省"
            value-key="provCode"
            @change="getCity"
          >
            <el-option
              v-for="item in provs"
              :key="item.provCode"
              :label="item.provName"
              :value="item"
            >
            </el-option>
          </el-select>
        </div>
        <div class="select1">
          <el-select
            v-model="city"
            placeholder="市"
            value-key="cityCode"
            @change="getCounty"
          >
            <el-option
              v-for="item in citys"
              :key="item.cityCode"
              :label="item.cityName"
              :value="item"
            >
            </el-option>
          </el-select>
        </div>
        <div class="select2">
          <el-select v-model="county" placeholder="区/县" value-key="areaCode">
            <el-option
              v-for="item in countys"
              :key="item.areaCode"
              :label="item.areaName"
              :value="item"
            >
            </el-option>
          </el-select>
        </div>
      </div>
      <div class="edit_row">
        <textarea
          class="address_txt"
          placeholder="请输入地址详情"
          id=""
          cols="30"
          rows="10"
          v-model="addressDetail"
          maxlength="60"
        ></textarea>
      </div>
    </div>
    <div class="operate">
      <div class="btn" @click="toSaveNew()">保存</div>
      <div class="btn" @click="cancel()">取消</div>
    </div>
  </div>
</template>
<script>
import {
  provGet,
  cityGet,
  countyGet,
  addAddress,
  updateAddress,
} from "/api/user.js";
export default {
  props: {
    info: {},
    userInfo: {},
  },
  data() {
    return {
      provs: [],
      citys: [],
      countys: [],
      prov: {},
      city: {},
      county: {},
      recieverName: "",
      recieverMobile: "",
      addressDetail: "",
    };
  },
  methods: {
    toAdd() {
      this.showAdd = false;
      this.clear();
    },
    edit(item) {
      if (!this.showAdd) {
        alert("先保存新地址");
      }
    },
    toSaveNew() {
      let info = this.info;
      if(!this.recieverName ||!this.recieverMobile ||!this.prov.provName ||!this.city.cityName ||!this.county.areaName ||!this.addressDetail ){
         this.$message({
            message: "请填写完整",
            type: "error",
          });
        return
      }
      if (info && info.addressId) {
        let formdata = new FormData();
        formdata.append("recieverName", this.recieverName);
        formdata.append("recieverMobile", this.recieverMobile);
        formdata.append("provName", this.prov.provName);
        formdata.append("provCode", this.prov.provCode);
        formdata.append("cityName", this.city.cityName);
        formdata.append("cityCode", this.city.cityCode);
        formdata.append("areaName", this.county.areaName);
        formdata.append("areaCode", this.county.areaCode);
        formdata.append("addressDetail", this.addressDetail);
        formdata.append("addressId", info.addressId);
        updateAddress(formdata).then((res) => {
          if (res && res.code && res.code == "200" && res.data) {
            this.$emit("refreshList");
          }
        });
      } else {
        let formdata = new FormData();
        formdata.append("recieverName", this.recieverName);
        formdata.append("recieverMobile", this.recieverMobile);
        formdata.append("provName", this.prov.provName);
        formdata.append("provCode", this.prov.provCode);
        formdata.append("cityName", this.city.cityName);
        formdata.append("cityCode", this.city.cityCode);
        formdata.append("areaName", this.county.areaName);
        formdata.append("areaCode", this.county.areaCode);
        formdata.append("addressDetail", this.addressDetail);
        addAddress(formdata).then((res) => {
          if (res && res.code && res.code == "200" && res.data) {
            this.$emit("refreshList", true);
          }
        });
      }
    },
    cancel() {
      if (this.info && this.info.addressId) {
        this.$emit("noedit");
      } else {
        this.$emit("noadd");
      }
    },
    clear() {
      this.recieverName = "";
      this.recieverMobile = "";
      this.addressDetail = "";
      this.prov = "";
      this.citys = [];
      this.city = "";
      this.countys = [];
      this.county = "";
    },
    // 获取地址信息
    getProv() {
      provGet().then((res) => {
        if (res && res.code && res.code == "200" && res.data)
          this.provs = res.data;
      });
    },
    getCity(prov) {
      let params = {
        params: {
          provCode: prov.provCode,
        },
      };
      cityGet(params).then((res) => {
        if (res && res.code && res.code == "200" && res.data)
          this.citys = res.data;
      });
    },
    getCounty(city) {
      let params = {
        params: {
          cityCode: city.cityCode,
        },
      };
      countyGet(params).then((res) => {
        if (res && res.code && res.code == "200" && res.data) {
          this.countys = res.data;
        }
      });
    },
  },
  created() {},
  mounted() {
    this.getProv();
    let info = this.info;
    if (!!info) {
      this.prov = {
        provName: info.provName,
        provCode: info.provCode,
      };
      this.getCity(this.prov);
      this.city = {
        cityName: info.cityName,
        cityCode: info.cityCode,
      };
      this.getCounty(this.city);
      this.county = {
        areaName: info.areaName,
        areaCode: info.areaCode,
      };
      this.recieverName = info.recieverName;
      this.recieverMobile = info.recieverMobile;
      this.addressDetail = info.addressDetail;
    }
  },
  watch: {
    prov(newName, oldName) {
      if (newName != oldName && oldName && oldName.provCode) {
        this.citys = [];
        this.city = "";
        this.countys = [];
        this.county = "";
      }
    },
    city(newName, oldName) {
      if (newName != oldName && oldName && oldName.cityCode) {
        this.countys = [];
        this.county = "";
      }
    },
  },
};
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
.info_input {
  width: 468px;
  height: 40px;
  background: #ffffff;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 2px;
  font-size: 14px;
  line-height: 40px;
  padding-left: 12px;
}
.select1 {
  width: 128px;
  height: 40px;
  margin-right: 48px;
}
.exist_address {
  margin-top: 46px;
  margin-left: 56px;
  display: flex;

  .adddress_info {
    width: 680px;
    border-right: 2px solid #d9d9d9;
    padding-right: 72px;

    .person {
      line-height: 38px;
      font-size: 14px;
      color: #333333;
    }
  }

  .operate {
    margin-left: 24px;
    display: flex;
    align-items: center;
    width: 100px;
    flex-wrap: wrap;

    .btn {
      width: 92px;
      height: 32px;
      background: #ffffff;
      border: 1px solid #d9d9d9;
      border-radius: 2px;
      font-size: 14px;
      color: rgba(0, 0, 0, 0.65);
      text-align: center;
      line-height: 32px;
      cursor: pointer;
    }
  }

  .edit_btn {
    border-right: 2px solid #d9d9d9;
    width: 680px;

    .edit_row {
      margin-top: 19px;
      display: flex;
    }

    .input_name {
      width: 160px;
    }

    .input_phone {
      width: 195px;
      margin-left: 48px;
    }

    .address_txt {
      width: 468px;
      height: 88px;
      line-height: 24px;
      background: #ffffff;
      border: 1px solid #d9d9d9;
      border-radius: 2px;
      padding: 10px 16px;
    }
  }
}

.add {
  width: 98px;
  height: 32px;
  background: #ffffff;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 2px;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.65);
  line-height: 32px;
  margin: 24px 0 0 56px;
  text-align: center;
  cursor: pointer;

  img {
    width: 16px;
    height: 16px;
    position: relative;
    top: 3px;
  }

  span {
    margin-left: 1px;
  }
}
</style>


