<template>
  <div class="product_list">
    <div class="product_main">
      <cata-log :page="page"></cata-log>
      <div class="userinfo">
        <div class="title">用户信息</div>
        <div class="send_info">
          <div class="info_row">
            <div class="info_name">用户名：</div>
            <input
              class="info_input"
              type="text"
              v-model="userInfo.username"
              placeholder="请输入"
              maxlength="64"
            />
          </div>
          <div class="info_row">
            <div class="info_name">邮箱：</div>
            <input
              class="info_input"
              type="text"
              v-model="userInfo.email"
              placeholder="请输入"
              maxlength="40"
              @blur="checkEmail($event)"
            />
            <div class="email_err" v-if="showEmailErr">
              <img class="errimg" src="../../assets/img/ic_error@2x.png" />
              邮箱输入错误
            </div>
          </div>

          <div class="info_row">
            <div class="info_name">电话：</div>
            <input
              class="info_input"
              type="text"
              v-model="userInfo.phone"
              placeholder="请输入"
              maxlength="11"
            />
          </div>
        </div>
        <template v-for="(item, k) of allAddress">
          <div class="exist_address" :key="k" v-if="item.status == 'show'">
            <div class="adddress_info">
              <div class="person">
                {{ item.recieverName }} {{ item.recieverMobile }}
              </div>
              <div class="person">
                {{ item.provName }}{{ item.cityName }}{{ item.areaName
                }}{{ item.addressDetail }}
              </div>
            </div>
            <div class="operate">
              <div class="btn" @click="edit(item)">编辑</div>
              <div class="btn" @click="del(item)">删除</div>
            </div>
          </div>
          <new-address
            v-else
            :info="item"
            :key="k"
            @noedit="noedit(item)"
            @refreshList="refreshList"
          ></new-address>
        </template>
        <div class="add" @click="toAdd()" v-if="showAdd">
          <img src="../../assets/img/list_ico_add@2x.png" alt="" />
          <span>增加地址</span>
        </div>
        <new-address
          v-else
          :info="[]"
          @noadd="noadd()"
          :userInfo="userInfo"
          @refreshList="refreshList"
        ></new-address>
        <!-- <div class="title">修改密码</div>
        <div class="send_info">
          <div class="info_row">
            <div class="info_name">原密码：</div>
            <input
              class="info_input"
              type="text"
              v-model="oldPassword"
              placeholder="请输入"
              maxlength="64"
            />
          </div>
          <div class="info_row">
            <div class="info_name">新密码：</div>
            <input
              class="info_input"
              type="password"
              v-model="passwordNew"
              placeholder="请输入"
              maxlength="64"
            />
          </div>
          <div class="info_row">
            <div class="info_name">确认密码：</div>
            <input
              class="info_input"
              type="password"
              v-model="passwordNewEnsure"
              placeholder="请输入"
              maxlength="64"
            />
          </div>
        </div> -->
        <div class="admit" @click="ensure()">确认修改</div>
      </div>
    </div>
    <div class="bottom"></div>
  </div>
</template>
<script>
import {
  provGet,
  cityGet,
  countyGet,
  update,
  changePass,
  userGet,
  queryAllAddress,
  deleteAddress,
  submitCellService,
  getCellOrder,
  getCellService,
} from "/api/user.js";
import CataLog from "/page/mine/catalog";
import NewAddress from "/page/components/newaddress";
export default {
  data() {
    return {
      provs: [],
      citys: [],
      countys: [],
      prov: "",
      city: "",
      county: "",
      oldPassword: "1",
      passwordNew: "2",
      passwordNewEnsure: "2",
      userInfo: {},
      page: "personal",
      allAddress: [],
      showAdd: true,
      name: "",
      phone: "",
      addressdetail: "",
      showEmailErr: false,
    };
  },
  methods: {
    del(item) {
      let formdata = new FormData();
      formdata.append("addressId", item.addressId);
      deleteAddress(formdata).then((res) => {
        if (res && res.code && res.code == "200" && res.data) {
          this.refreshList();
        }
      });
    },
    toAdd() {
      this.showAdd = false;
    },
    noadd() {
      this.showAdd = true;
    },
    edit(item) {
      item.status = "";
    },
    noedit(item) {
      item.status = "show";
    },
    refreshList(status) {
      if (status) {
        this.noadd();
      }
      queryAllAddress().then((res) => {
        if (res && res.code && res.code == "200" && res.data) {
          res.data.forEach((item) => {
            item.status = "show";
          });
          this.allAddress = res.data;
        }
      });
    },
    getUser() {
      userGet().then((res) => {
        if (res && res.code && res.code == "200" && res.data)
          this.userInfo = res.data;
      });
      // submitCellService().then((res) => {
      //   if (res && res.code && res.code == "200" && res.data){
      //   }
      // });
      let params = {
        params: {
          taskId: "1",
        },
      };
      getCellOrder(params).then((res) => {
        if (res && res.code && res.code == "200" && res.data) {
        }
      });
      getCellService(params).then((res) => {
        if (res && res.code && res.code == "200" && res.data) {
        }
      });
    },
    // 确定修改
    ensure() {
      // if (this.passwordNew != this.passwordNewEnsure) {
      //   this.$message({
      //     message: "俩次密码输入不同，请修改后重试",
      //     type: "error",
      //   });
      //   return;
      // }
      let params = {
        params: this.userInfo,
      };
      update(params).then((res) => {
        if (res && res.code && res.code == "200" && res.data) {
          this.getUser();
        }
      });
      // let paramsPass = {
      //   params: {
      //     oldPassword: this.oldPassword,
      //     password: this.passwordNew,
      //   },
      // };
      // changePass(paramsPass).then((res) => {
      //   if (res && res.code && res.code == "200" && res.data)
      //     this.citys = res.data;
      // });
    },
    checkEmail(e) {
      const { value } = e.target;
      var reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
      if (reg.test(value)) {
        this.showEmailErr = false;
      } else {
        this.showEmailErr = true;
      }
    },
  },
  created() {},
  mounted() {
    this.getUser();
    this.refreshList();
  },
  watch: {
    prov(newName, oldName) {
      if (newName != oldName) {
        this.citys = [];
        this.city = "";
        this.countys = [];
        this.county = "";
      }
    },
    city(newName, oldName) {
      if (newName != oldName) {
        this.countys = [];
        this.county = "";
      }
    },
  },
  components: {
    CataLog,
    NewAddress,
  },
};
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
.product_list {
  background: #f0faff;
  width: 1440px;
  margin: 0 auto;
  padding-top: 24px;
}
.bottom {
  height: 24px;
}
.product_main {
  width: 1200px;
  background: #ffffff;
  margin: 0 auto;
  padding: 1px 0;
  display: flex;
  .userinfo {
    width: 946px;
    .title {
      height: 63px;
      margin-left: 24px;
      font-size: 16px;
      color: rgba(0, 0, 0, 0.85);
      line-height: 24px;
      border-bottom: 1px solid #e9e9e9;
      width: 920px;
      font-weight: bold;
      padding: 24px 0 15px 36px;
    }
    .info_input {
      width: 468px;
      height: 40px;
      background: #ffffff;
      border: 1px solid rgba(0, 0, 0, 0.15);
      border-radius: 2px;
      font-size: 14px;
      line-height: 40px;
      padding-left: 12px;
    }
    .send_info {
      margin: 30px 56px;
      .info_row {
        display: flex;
        margin-top: 20px;
        .info_name {
          text-align: right;
          line-height: 40px;
          width: 85px;
          font-size: 14px;
          color: rgba(0, 0, 0, 0.85);
        }
        .el-date-editor.el-input,
        .el-date-editor.el-input__inner {
          width: 468px;
        }
      }
    }
    .exist_address {
      margin-top: 46px;
      margin-left: 56px;
      display: flex;
      .adddress_info {
        width: 680px;
        border-right: 2px solid #d9d9d9;
        padding-right: 72px;
        .person {
          line-height: 38px;
          font-size: 14px;
          color: #333333;
        }
      }
      .operate {
        margin-left: 24px;
        display: flex;
        align-items: center;
        width: 100px;
        flex-wrap: wrap;
        .btn {
          width: 92px;
          height: 32px;
          background: #ffffff;
          border: 1px solid #d9d9d9;
          border-radius: 2px;
          font-size: 14px;
          color: rgba(0, 0, 0, 0.65);
          text-align: center;
          line-height: 32px;
          cursor: pointer;
        }
      }
      .edit_btn {
        border-right: 2px solid #d9d9d9;
        width: 680px;
        .edit_row {
          margin-top: 19px;
          display: flex;
        }
        .input_name {
          width: 160px;
        }
        .input_phone {
          width: 195px;
          margin-left: 48px;
        }
        .address_txt {
          width: 468px;
          height: 88px;
          background: #ffffff;
          border: 1px solid #d9d9d9;
          border-radius: 2px;
          padding: 5px 12px;
        }
      }
    }
    .add {
      width: 98px;
      height: 32px;
      background: #ffffff;
      border: 1px solid rgba(0, 0, 0, 0.15);
      border-radius: 2px;
      font-size: 14px;
      color: rgba(0, 0, 0, 0.65);
      line-height: 32px;
      margin: 46px 0 0 56px;
      text-align: center;
      cursor: pointer;
      img {
        width: 16px;
        height: 16px;
        position: relative;
        top: 3px;
      }
      span {
        margin-left: 1px;
      }
    }
  }
}
.select1 {
  width: 128px;
  height: 40px;
  margin-right: 48px;
}
.admit {
  width: 98px;
  height: 32px;
  background: #0695d4;
  border-radius: 2px;
  font-size: 14px;
  color: #ffffff;
  text-align: center;
  line-height: 32px;
  margin: 45px 0 36px 790px;
  cursor: pointer;
}
.email_err {
  font-size: 12px;
  color: #ff4c4c;
  line-height: 24px;
  position: relative;
  left: 10px;
  top: 12px;
  img {
    position: relative;
    top: 1px;
    left: 0;
    width: 11px;
    height: 11px;
  }
}
</style>



